<template>
    <el-container v-if="true" class="view-container">
        <el-header style="height: auto;min-height: 60px;" class="view-header">
            <NavComponent></NavComponent>
        </el-header>
        <el-main class="view-main">
            <MainComponent></MainComponent>
        </el-main>
        <el-footer class="view-footer" style="height:auto">
            <FooterComponent></FooterComponent>
        </el-footer>
    </el-container>
</template>

<script>
import NavComponent from "@/components/NavComponent/";
import MainComponent from "@/components/MainComponent/";
import FooterComponent from "@/components/FooterComponent/";
export default {
    name: 'Pbulic',
    components: {
        NavComponent,
        MainComponent,
        FooterComponent
    },
    data() {
        return {
            
        }
    },
    computed: {

    },
    watch: {
    },
    methods: {

    },
    created() {

    },
    mounted() {

    },
    beforeCreate() { },
    beforeMount() { },
    beforeUpdate() { },
    updated() { },
    beforeDestroy() { },
    destroyed() { },
    activated() { },
}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.view {
    &-container,
    &-main,
    &-header,
    &-footer {
        padding: 0;
        margin: 0;
        overflow: hidden;
    }

    &-container {
        height: 100%;
    }

    &-main {
        height: 300px;
        min-height: 50%;
        max-height: 80%;
    }

    &-footer {
        background-color: rgba(255, 255, 255, 0.7);
    }
}</style>


